<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="input-props"><a class="header-anchor" href="#input-props" aria-hidden="true">¶</a> Input Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>min</td>
<td>最小值</td>
<td>Number</td>
<td>-Infinity</td>
</tr>
<tr>
<td>max</td>
<td>最大值</td>
<td>Number</td>
<td>Infinity</td>
</tr>
<tr>
<td>value</td>
<td>当前值</td>
<td>Number</td>
<td>-</td>
</tr>
<tr>
<td>step</td>
<td>每次改变步数，可以为小数</td>
<td>Number</td>
<td>1</td>
</tr>
<tr>
<td>disabled</td>
<td>禁用</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>size</td>
<td>输入框大小</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="input-events"><a class="header-anchor" href="#input-events" aria-hidden="true">¶</a> Input Events</h3>
<table class="table"><thead>
<tr>
<th>事件</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>变化回调事件</td>
<td>value</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
export default {
    data() {
        return {
            value: 5,
            disabled: true
        }
    },
    methods: {
        _handleChange(value) {
            console.log('changed ', value)
        },
        _toggle() {
            this.disabled = !this.disabled
        }
    }
}
</script>


